const isDev = process.env.NODE_ENV === "development";

const urlPrefix = isDev ? "/api" : "";

export async function getShopcart(userID) {
  try {
    const res = await fetch(`${urlPrefix}/shopcarts/${userID}`);
    return res.json();
  } catch (err) {
    console.log(err);
    throw err;
  }
}

export async function addItem(userID, productID, price, quantity) {
  try {
    const item = {
      product_id: productID,
      price,
      quantity,
      time_added: new Date(),
    };
    await fetch(`${urlPrefix}/shopcarts/${userID}`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(item), // body data type must match "Content-Type" header
    });
    return await getShopcart(userID);
  } catch (err) {
    console.log("err", err);
    throw err;
  }
}

export async function updateItem(userID, productID, price, quantity) {
  try {
    const product = { product_id: productID, price, quantity };
    await fetch(`${urlPrefix}/shopcarts/${userID}/items/${productID}`, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(product), // body data type must match "Content-Type" header
    });

    return await getShopcart(userID);
  } catch (err) {
    throw err;
  }
}

export async function deleteItem(userID, productID) {
  try {
    await fetch(`${urlPrefix}/shopcarts/${userID}/items/${productID}`, {
      method: "DELETE",
    });

    return await getShopcart(userID);
  } catch (err) {
    throw err;
  }
}

export async function clearCart(userID) {
  try {
    await fetch(`${urlPrefix}/shopcarts/${userID}`, {
      method: "DELETE",
    });

    return await getShopcart(userID);
  } catch (err) {
    throw err;
  }
}
